@import '../../styles/colors';

.home-page {
  .tag-cont {
    padding-left: 16px;
  }
  .intro {
    font-family: 'Poppins', sans-serif;
    color: $white;
    font-size: 46px;
    @media only screen and (max-width: 1200px) {
      font-size: 40px;
    }
    @media only screen and (max-width: 900px) {
      font-size: 36px;
    }
    @media only screen and (max-width: 600px) {
      font-size: 30px;
    }
    @media only screen and (max-width: 500px) {
      font-size: 24px;
    }
  }
  .bio {
    padding-top: 16px;
    font-size: 14px;
    color: $text-dark-5;
    padding-bottom: 36px;
    letter-spacing: 1px;
    @media only screen and (max-width: 500px) {
      padding-top: 26px;
      padding-bottom: 46px;
      font-size: 12px;
    }
  }
  .breaker {
    display: none;
    @media only screen and (max-width: 500px) {
      display: block;
    }
  }
  .shadow {
    color: $secondary-color;
    font-size: 54px;
    @media only screen and (max-width: 1200px) {
      font-size: 46px;
    }
    @media only screen and (max-width: 900px) {
      font-size: 40px;
    }
    @media only screen and (max-width: 600px) {
      font-size: 36px;
    }
    @media only screen and (max-width: 500px) {
      font-size: 30px;
    }
    &:hover {
      color: $primary-color;
    }
  }
  .shadow2 {
    color: $primary-color;
    font-size: 54px;
    @media only screen and (max-width: 1200px) {
      font-size: 46px;
    }
    @media only screen and (max-width: 900px) {
      font-size: 40px;
    }
    @media only screen and (max-width: 600px) {
      font-size: 36px;
    }
    @media only screen and (max-width: 500px) {
      font-size: 30px;
    }
    &:hover {
      color: $secondary-color;
    }
  }
  .right-pane {
    z-index: -50;
    position: fixed;
    right: 0;
    bottom: 0;
    height: 100%;
    @media only screen and (min-height: 720px) {
      height: 90%;
    }
    @media only screen and (min-height: 840px) {
      height: 80%;
    }
    @media only screen and (max-width: 1200px) {
      height: 80%;
    }
    @media only screen and (max-width: 1100px) {
      height: 70%;
    }
    @media only screen and (max-width: 800px) {
      height: 60%;
    }
    @media only screen and (max-width: 500px) {
      height: 50%;
    }
  }
}
