@import '../../../styles/colors';

#adarsh-input {
  input {
    width: 100%;
    border: none;
    background: $input-bg-color;
    padding: 16px;
    font-size: 16px;
    color: $text-dark-5;
    outline: none;
    box-sizing: border-box;
    &:focus {
      border: 1px solid $primary-color;
    }
  }
}
