@import '../../../styles/colors';

#adarsh-button {
  color: $white;
  .button {
    border: 1px solid $primary-color;
    color: $primary-color;
    display: inline-block;
    padding: 8px 32px;
    cursor: pointer;
    transition: 0.3s all ease-in-out;
    background: transparent;
    box-sizing: border-box;
    font-size: 14px;
    &:hover {
      transform: scale(1.1);
      background: $primary-color;
      color: $bg-color;
    }
  }

  .disabled,
  .loading {
    border: 1px solid $primary-color;
    color: $primary-color;
    display: inline-block;
    padding: 8px 32px;
    cursor: no-drop;
    box-sizing: border-box;
    font-size: 14px;
  }

  .tertiary {
    border-radius: 50px;
    padding: 10px 18px;
  }
}
